.sidebarOption{
    display: flex;
    align-items: center;
    cursor: pointer;
}
.sidebarOption > .MuiSvgIcon-root {
    padding: 20px;
    
}

.sidebarOption h2{
    font-weight: 800;
    font-size: 20px;
    margin-right: 20px;
}

.sidebarOption:hover{
    background-color:#e8f5fe;
    border-radius: 30px;
    color: rgba(29,161,242,1.00);
    /* color: var(--twitter-color); */
    transition: color 100ms ease-out;

}